import { getPickersLocalization } from './utils/getPickersLocalization';
// This object is not Partial<PickersLocaleText> because it is the default values
const isISPickers = {
  // Calendar navigation
  previousMonth: 'Fyrri mnuur',
  nextMonth: 'Nsti mnuur',
  // View navigation
  openPreviousView: 'opna fyrri skoun',
  openNextView: 'opna nstu skoun',
  calendarViewSwitchingButtonAriaLabel: view => view === 'year' ? 'rsskoun er opin, skipta yfir  dagatalsskoun' : 'dagatalsskoun er opin, skipta yfir  rsskoun',
  inputModeToggleButtonAriaLabel: (isKeyboardInputOpen, viewType) => {
    const viewTypeTranslated = viewType === 'calendar' ? 'dagatals' : 'klukku';
    return isKeyboardInputOpen ? `textainnslttur er opinn, fara  ${viewTypeTranslated}skoun` : `${viewTypeTranslated}skoun er opin, opna fyrir textainnsltt`;
  },
  // DateRange placeholders
  start: 'Upphaf',
  end: 'Endir',
  // Action bar
  cancelButtonLabel: 'Htta vi',
  clearButtonLabel: 'Hreinsa',
  okButtonLabel: 'OK',
  todayButtonLabel: ' dag',
  // Toolbar titles
  datePickerDefaultToolbarTitle: 'Velja dagsetningu',
  dateTimePickerDefaultToolbarTitle: 'Velja dagsetningu og tma',
  timePickerDefaultToolbarTitle: 'Velja tma',
  dateRangePickerDefaultToolbarTitle: 'Velja tmabil',
  // Clock labels
  clockLabelText: (view, time, adapter) => `Select ${view}. ${time === null ? 'Enginn tmi valinn' : `Valinn tmi er ${adapter.format(time, 'fullTime')}`}`,
  hoursClockNumberText: hours => `${hours} klukkustundir`,
  minutesClockNumberText: minutes => `${minutes} mntur`,
  secondsClockNumberText: seconds => `${seconds} sekndur`,
  // Open picker labels
  openDatePickerDialogue: (rawValue, utils) => rawValue && utils.isValid(utils.date(rawValue)) ? `Velja dagsetningu, valin dagsetning er ${utils.format(utils.date(rawValue), 'fullDate')}` : 'Velja dagsetningu',
  openTimePickerDialogue: (rawValue, utils) => rawValue && utils.isValid(utils.date(rawValue)) ? `Velja tma, valinn tmi er ${utils.format(utils.date(rawValue), 'fullTime')}` : 'Velja tma',
  // Table labels
  timeTableLabel: 'velja tma',
  dateTableLabel: 'velja dagsetningu'
};
export const isIS = getPickersLocalization(isISPickers);