import { getPickersLocalization } from './utils/getPickersLocalization';
// maps ClockPickerView to its translation
const clockViews = {
  hours: 'Stunden',
  minutes: 'Minuten',
  seconds: 'Sekunden'
}; // maps PickersToolbar["viewType"] to its translation

const pickerViews = {
  calendar: 'Kalenderansicht',
  clock: 'Uhransicht'
};
const deDEPickers = {
  // Calendar navigation
  previousMonth: 'Letzter Monat',
  nextMonth: 'Nchster Monat',
  // View navigation
  openPreviousView: 'Letzte Ansicht ffnen',
  openNextView: 'Nchste Ansicht ffnen',
  calendarViewSwitchingButtonAriaLabel: view => view === 'year' ? 'Jahresansicht ist geffnet, zur Kalenderansicht wechseln' : 'Kalenderansicht ist geffnet, zur Jahresansicht wechseln',
  inputModeToggleButtonAriaLabel: (isKeyboardInputOpen, viewType) => isKeyboardInputOpen ? `Texteingabeansicht ist geffnet, zur ${pickerViews[viewType]} wechseln` : `${pickerViews[viewType]} ist geffnet, zur Texteingabeansicht wechseln`,
  // DateRange placeholders
  start: 'Beginn',
  end: 'Ende',
  // Action bar
  cancelButtonLabel: 'Abbrechen',
  clearButtonLabel: 'Lschen',
  okButtonLabel: 'OK',
  todayButtonLabel: 'Heute',
  // Toolbar titles
  datePickerDefaultToolbarTitle: 'Datum auswhlen',
  dateTimePickerDefaultToolbarTitle: 'Datum & Uhrzeit auswhlen',
  timePickerDefaultToolbarTitle: 'Uhrzeit auswhlen',
  dateRangePickerDefaultToolbarTitle: 'Datumsbereich auswhlen',
  // Clock labels
  clockLabelText: (view, time, adapter) => {
    var _clockViews$view;

    return `${(_clockViews$view = clockViews[view]) != null ? _clockViews$view : view} auswhlen. ${time === null ? 'Keine Uhrzeit ausgewhlt' : `Gewhlte Uhrzeit ist ${adapter.format(time, 'fullTime')}`}`;
  },
  hoursClockNumberText: hours => `${hours} ${clockViews.hours}`,
  minutesClockNumberText: minutes => `${minutes} ${clockViews.minutes}`,
  secondsClockNumberText: seconds => `${seconds}  ${clockViews.seconds}`,
  // Open picker labels
  openDatePickerDialogue: (rawValue, utils) => rawValue && utils.isValid(utils.date(rawValue)) ? `Datum auswhlen, gewhltes Datum ist ${utils.format(utils.date(rawValue), 'fullDate')}` : 'Datum auswhlen',
  openTimePickerDialogue: (rawValue, utils) => rawValue && utils.isValid(utils.date(rawValue)) ? `Uhrzeit auswhlen, gewhlte Uhrzeit ist ${utils.format(utils.date(rawValue), 'fullTime')}` : 'Uhrzeit auswhlen',
  // Table labels
  timeTableLabel: 'Uhrzeit auswhlen',
  dateTableLabel: 'Datum auswhlen'
};
export const deDE = getPickersLocalization(deDEPickers);